import { Edit } from "@mui/icons-material";
import { Box, IconButton, Rating } from "@mui/material";
import { Fragment, useState } from "react";
import CreateOrdenCompraModal from "./create-orden-compra";
import VisibilityIcon from '@mui/icons-material/Visibility';
import Delete from "icons/Delete";
import { Small } from "components/Typography";
const OrdenCompraColumns = [
    {
        Header: "Codigo",
        accessor: "codigo"
    },
    {
        Header: "Fecha",
        accessor: "fecha"
    },
    {
        Header: "Estado",
        accessor: "estado",
        Cell: ({
            row
        }) => {
            return (
                <Small sx={{
                    backgroundColor: "action.hover",
                    borderRadius: 10,
                    padding: ".2rem 1rem",
                    textAlign: "center"
                }}>
                    Creado
                </Small>
            )
        }
    },
    {
        Header: "Usuario",
        accessor: "usuario",
    },
    {
        Header: "Proveedor",
        accessor: "proveedor"
    },
    {
        Header: "Codigo Producto",
        accessor: "codigoProducto"
    },
    {
        Header: "Producto",
        accessor: "producto"
    },
    {
        Header: "Cantidad",
        accessor: "cantidad"
    },
    {
        Header: "Precio compra",
        accessor: "precioCompra"
    },
    {
        Header: "Total compra",
        accessor: "totalCompra"
    },
    {
        Header: "Almacen Destino",
        accessor: "almacenDestino"
    },
    {
        Header: "Facturado",
        accessor: "facturado"
    },
    {
        Header: "Acciones",
        accessor: "acciones",
        Cell: ({
            row
        }) => {
            const style = {
                fontSize: 19,
                transition: "color 0.3s",
                color: row.isSelected ? "white" : "text.disabled"
            };
            const [tipo, setTipo] = useState('');

            const [openModal, setOpenModal] = useState(false);
            return (
                <Fragment>
                    <IconButton onClick={() => setOpenModal(true)}>
                        <VisibilityIcon sx={{
                            fontSize: 18,
                            color: "text.disabled"
                        }} />
                    </IconButton>
                    <IconButton onClick={() => setOpenModal(true)}>
                        <Edit sx={{
                            fontSize: 18,
                            color: "text.disabled"
                        }} />
                    </IconButton>
                    <IconButton onClick={() => setOpenModal(true)}>
                        <Delete sx={style} />
                    </IconButton>
                    <CreateOrdenCompraModal editProduct open={openModal} data={row.original} onClose={() => setOpenModal(false)} />
                </Fragment>
            );
        }
    }];
export default OrdenCompraColumns;